import React from "react";
import "../index.css";

function Summary({data}) {

    //    
    const validEntries = data.filter(entry => !entry.isHoliday);

    // reduce   acc() 
    const satTotal = validEntries.filter(d => d.day === "").reduce((acc, cur) => acc+cur.returns, 0);
    const satInqur = validEntries.filter(d => d.day === "").reduce((acc, cur) => acc + cur.inqueries, 0);

    const sunTotal = validEntries.filter(d => d.day === "").reduce((acc, cur) => acc+cur.returns, 0);
    const sunInqur = validEntries.filter(d => d.day === "").reduce((acc, cur) => acc + cur.inqueries, 0);

    return (
        <div id="summary-content"  className="container">
            <p>----------------------------------------</p>
            
            <p>--</p>
            <p></p>
            <div>  : {satTotal}</div>
            <div>     : {satInqur}</div>
            <p></p>
            <div>  : {sunTotal}</div>
            <div>     : {sunInqur}</div>
        </div>
    );
}

export default Summary;