import React from "react";
import "../index.css"
import copy from "copy-to-clipboard";

function CopyButton({data}) {
    const handleCopy = () => {

        const month = new Date().getMonth() + 1;
        let report = `${month} _4  \n\n`

        data.forEach(d => {
            if (d.isHoliday) {
                report += `${d.week} / ${d.date} (${d.day}) / \n\n`
            } else {

                const finalStaff = d.customStaff.map((s, i) => (s.trim() ? s : d.staff[i])).join(", ");
                const returns = d.returns ? d.returns : 0;
                const inqueries = d.inqueries ? d.inqueries : 0;
                
                report += `${d.week} / ${d.date} (${d.day}) / ${finalStaff}\n`
                report += ` : ${returns}\n`;
                report += `() : ${inqueries}\n\n`;
            }
        });

        const summaryText = document.getElementById("summary-content").innerText;

        if (summaryText) {
            report += summaryText;
        }

        if(copy(report)) {
            alert("");
        } else {
            alert(" ");
        }
    };

    return <button className="button" onClick={handleCopy}> </button>;
}

export default CopyButton;